import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import React, { Suspense, lazy } from "react";
import "./App.css";
import AppHeader from "./AppHeader.jsx";
import AppFooter from "./AppFooter.jsx";
import { MDBContainer } from "mdbreact";

const Home = lazy(() => import("../home/Home"));
const CompreAqui = lazy(() => import("../compreaqui/CompreAqui"));
const Camarotes = lazy(() => import("../camarotes/Camarotes"));
const Blocos = lazy(() => import("../blocos/Blocos"));
const Navios = lazy(() => import("../navios/Navios"));
const Hoteis = lazy(() => import("../hoteis/Hoteis"));
const Atendimento = lazy(() => import("../atendimento/Atendimento"));
const MinhaConta = lazy(() => import("../minhaconta/MinhaConta"));

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    this.setState({});
  }
  render() {
    const containerStyle = { maxWidth: "600px" };
    return (
      <div className="App">
        <Router>
          <MDBContainer style={containerStyle}>
            <AppHeader />

            <Suspense fallback={<div>Carregando...</div>}>
              <Switch>
                <Route exact path="/" component={Home} />
                <Route path="/compre-aqui" component={CompreAqui} />
                <Route path="/camarotes" component={Camarotes} />
                <Route path="/blocos" component={Blocos} />
                <Route path="/navios" component={Navios} />
                <Route path="/hoteis" component={Hoteis} />
                <Route path="/atendimento" component={Atendimento} />
                <Route path="/minha-conta" component={MinhaConta} />
              </Switch>
            </Suspense>

            <AppFooter />
          </MDBContainer>
        </Router>
      </div>
    );
  }
}

export default App;
